// src/util/formatCurrency.js
export function formatCurrency(value) {
  //  undefined null 0 
  if (value === undefined || value === null) {
    value = 0;
  }

  if (value >= 100000000) {
    //    
    const billions = Math.floor(value / 100000000); //  
    const remaining = Math.floor((value % 100000000) / 10000); //   
    return remaining > 0 ? `${billions} ${remaining}` : `${billions}`;
  } else if (value >= 10000) {
    return `${Math.floor(value / 10000)}`; //   
  } else {
    return `${value}`; //      
  }
}

export function formatCurrency2(value) {
  //  undefined null 0 
  if (value === undefined || value === null) {
    value = 0;
  }

  if (value >= 100000000) {
    //    
    const billions = Math.floor(value / 100000000); //  
    const remaining = Math.floor((value % 100000000) / 10000); //   
    return (
      <>
        {billions}
        <span className="currency-unit"></span>
        {remaining > 0 && (
          <>
            {remaining}
            <span className="currency-unit"></span>
          </>
        )}
      </>
    );
  } else if (value >= 10000) {
    return (
      <>
        {Math.floor(value / 10000)}
        <span className="currency-unit"></span>
      </>
    );
  } else {
    return (
      <>
        {value}
        <span className="currency-unit"></span>
      </>
    );
  }
}
