// src/pages/Stats/Stats.js
import React, { useState } from "react";
import { Dropdown } from "primereact/dropdown";
import { Panel } from "primereact/panel";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Chart } from "primereact/chart";
import "./Stats.scss";
import { useStats } from "../../hooks/useStats";
import { formatCurrency, formatCurrency2 } from "../../util/formatCurrency";
import { useNavigate } from "react-router-dom";

function Stats() {
  const {
    topSales,
    avgSalesInfo,
    lowestFranchiseFeeStores,
    newOpeningsCount,
    region,
    setRegion,
    industry,
    setIndustry,
    limit,
    setLimit,
  } = useStats();

  const [isTopSalesExpanded, setIsTopSalesExpanded] = useState(false);
  const [isLowestFeeExpanded, setIsLowestFeeExpanded] = useState(false);

  const regionOptions = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];

  const industryOptions = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: " ", value: " " },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: " ", value: " " },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "/", value: "/" },
    { label: " ( )", value: " ( )" },
  ];

  const limitOptions = [
    { label: 10, value: 10 },
    { label: 20, value: 20 },
    { label: 100, value: 100 },
  ];

  const navigate = useNavigate();

  const validSalesInfo = avgSalesInfo.filter(
    (info) =>
      info.industry_avg_monthly_sales !== undefined &&
      info.industry_avg_monthly_sales !== null
  );
  const totalAverageSales =
    validSalesInfo.length > 0
      ? validSalesInfo.reduce(
          (sum, info) => sum + info.industry_avg_monthly_sales,
          0
        ) / validSalesInfo.length
      : 0;
  const specificIndustryInfo = avgSalesInfo.find(
    (info) => info.industry === industry
  );
  const specificIndustrySales =
    industry === ""
      ? totalAverageSales
      : specificIndustryInfo
      ? specificIndustryInfo.industry_avg_monthly_sales
      : 0;

  const difference = specificIndustrySales - totalAverageSales;
  const isHigher = difference > 0;

  const comparisonIndicator = isHigher ? "" : "";
  const comparisonText = `${comparisonIndicator} ${formatCurrency(
    Math.abs(difference)
  )}`;

  const combinedData = {
    labels: avgSalesInfo.map((info) => info.industry),
    datasets: [
      {
        label: " ",
        data: avgSalesInfo.map((info) => info.industry_avg_monthly_sales),
        backgroundColor: "#42A5F5",
        yAxisID: "y-axis-sales",
      },
      {
        label: "  ",
        data: newOpeningsCount.map((opening) => opening.new_openings_count),
        backgroundColor: "#66BB6A",
        yAxisID: "y-axis-openings",
      },
    ],
  };

  const chartOptions = {
    maintainAspectRatio: false,
    plugins: {
      legend: {
        labels: {
          font: {
            family: "Maplestory Bold",
          },
        },
      },
      tooltip: {
        titleFont: {
          family: "Maplestory Bold",
        },
        bodyFont: {
          family: "Maplestory Bold",
        },
      },
    },
    scales: {
      x: {
        ticks: {
          font: {
            family: "Maplestory Bold",
            size: "14px",
          },
        },
      },
      "y-axis-sales": {
        type: "linear",
        position: "left",
        ticks: {
          font: {
            family: "Maplestory Bold",
            size: "16px",
          },
          callback: function (value) {
            return formatCurrency(value);
          },
        },
      },
      "y-axis-openings": {
        type: "linear",
        position: "right",
        ticks: {
          font: {
            family: "Maplestory Bold",
            size: "16px",
          },
          callback: function (value) {
            return value + "";
          },
        },
      },
    },
  };

  const handleStoreNameClick = (storeName) => {
    navigate(`/search?storeName=${encodeURIComponent(storeName)}`);
  };

  return (
    <div className="pages-container">
      <div className="pages-header card">
        <span className="header-title"> </span>
        <div className="header-selects">
          <Dropdown
            value={region}
            options={regionOptions}
            onChange={(e) => setRegion(e.value)}
            placeholder=" "
            className="dropdown"
          />
          <Dropdown
            value={industry}
            options={industryOptions}
            onChange={(e) => setIndustry(e.value)}
            placeholder=" "
            className="dropdown"
          />
          <Dropdown
            value={limit}
            options={limitOptions}
            onChange={(e) => setLimit(e.value)}
            placeholder="  "
            className="dropdown"
          />
        </div>
      </div>
      <div className="stats-summary card">
        <div className="summary-box">
          <h4>
            {region} <br></br>  
          </h4>
          <p>{formatCurrency2(totalAverageSales)}</p>
        </div>
        <div className="summary-box">
          <h4>
            {region} <br></br>
            {industry}  
          </h4>
          <p>
            {formatCurrency2(specificIndustrySales)}
            {difference !== 0 && (
              <span
                className="comparison"
                style={{ color: isHigher ? "red" : "blue" }}
              >
                {comparisonText.replace("", "")}
                <span className="currency-unit"></span>
              </span>
            )}
          </p>
          {/* <p>
            {formatCurrency(specificIndustrySales)}
            {difference !== 0 && (
              <span
                className="comparison"
                style={{ color: isHigher ? "red" : "blue" }}
              >
                {comparisonText}
              </span>
            )}
          </p> */}
        </div>
      </div>
      <div className="card">
        <Panel
          header="      "
          toggleable
          className="panel"
        >
          <Chart type="bar" data={combinedData} options={chartOptions} />
        </Panel>
      </div>

      {/*    */}
      <div className="card">
        <Panel header="  " toggleable className="panel">
          <DataTable
            value={topSales}
            paginator
            rows={5}
            className="basic-table"
            onRowClick={(e) => {
              handleStoreNameClick(e.data.store_name);
            }}
            rowClassName={() => "table-row"}
            emptyMessage=" ."
          >
            <Column
              field="store_name"
              header=""
              body={(rowData) => (
                <span className="store-link">{rowData.store_name}</span>
              )}
            />
            <Column
              field="avg_monthly_sales"
              header=" "
              body={(rowData) => formatCurrency(rowData.avg_monthly_sales)}
            />
            <Column
              field="franchise_fee"
              header=" "
              body={(rowData) => formatCurrency(rowData.franchise_fee)}
            />
            <Column field="store_count" header=" " />
          </DataTable>
        </Panel>
      </div>

      {/*     */}
      <div className="card">
        <Panel header="   " toggleable className="panel">
          <DataTable
            value={lowestFranchiseFeeStores}
            paginator
            rows={5}
            className="basic-table"
            onRowClick={(e) => {
              handleStoreNameClick(e.data.store_name);
            }}
            rowClassName={() => "table-row"}
            emptyMessage=" ."
          >
            <Column
              field="store_name"
              header=""
              body={(rowData) => (
                <span className="store-link">{rowData.store_name}</span>
              )}
            />
            <Column
              field="avg_monthly_sales"
              header=" "
              body={(rowData) => formatCurrency(rowData.avg_monthly_sales)}
            />
            <Column
              field="franchise_fee"
              header=" "
              body={(rowData) => formatCurrency(rowData.franchise_fee)}
            />
            <Column field="industry" header="" />
          </DataTable>
        </Panel>
      </div>
    </div>
  );
}

export default Stats;
