// src/pages/HelpInfo/HelpInfoAdmin.js
import React, { useState, useEffect } from "react";
import { Button } from "primereact/button";
import { Panel } from "primereact/panel";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { useHelpInfo } from "../../hooks/useHelpInfo";
import { useLogin } from "../../hooks/useLogin";
// import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { DragDropContext, Droppable, Draggable } from "@hello-pangea/dnd";
import "./HelpInfoAdmin.scss";

function HelpInfoAdmin() {
  const {
    region,
    setRegion,
    siteList,
    setSiteList,
    saveNewOrder,
    addSite,
    deleteSite,
    updateSite,
    supportList,
    addSupportBoard,
    deleteSupportBoard,
    updatesupportBoard,
  } = useHelpInfo();

  const { adminCheck } = useLogin();

  useEffect(() => {
    adminCheck();
  }, []);

  const [showDialog, setShowDialog] = useState(false);
  const [isEditMode, setIsEditMode] = useState(false); //   
  const [editingSite, setEditingSite] = useState(null); //     
  const [formData, setFormData] = useState({
    img: "",
    link: "",
    title: "",
    description: "",
  });
  const [fileError, setFileError] = useState("");

  //    
  const onDragEnd = (result) => {
    const { destination, source } = result;

    if (!destination || destination.index === source.index) return;

    const updatedList = Array.from(siteList);
    const [movedItem] = updatedList.splice(source.index, 1);
    updatedList.splice(destination.index, 0, movedItem);

    const reorderedList = updatedList.map((item, idx) => ({
      ...item,
      indexOrder: idx + 1,
    }));

    setSiteList(reorderedList);
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = () => {
      setFormData({ ...formData, img: reader.result }); // base64   
    };
    reader.onerror = () => setFileError("  .");
    reader.readAsDataURL(file);
  };

  const handleInputChange = (e, type) => {
    const { name, value } = e.target;

    if (type === "site") {
      setFormData({ ...formData, [name]: value });
    } else {
      setSupportFormData({ ...supportformData, [name]: value });
    }
  };

  const handleSiteSave = async () => {
    try {
      if (isEditMode) {
        //  
        const img = formData.img
          ? formData.img.replace(/^data:image\/\w+;base64,/, "")
          : null; //    null
        await updateSite(
          editingSite.siteInfoId,
          img,
          formData.link,
          formData.title,
          formData.description
        );
        alert("  .");
      } else {
        //  
        const base64Image = formData.img.replace(
          /^data:image\/\w+;base64,/,
          ""
        );
        await addSite(
          base64Image,
          formData.link,
          formData.title,
          formData.description
        );
        alert("  .");
      }
    } catch (error) {
      alert(
        isEditMode
          ? "    ."
          : "    ."
      );
    } finally {
      setShowDialog(false);
      setFormData({ img: "", link: "", title: "", description: "" });
      setIsEditMode(false);
      setEditingSite(null);
    }
  };

  const handleDeleteSite = async (siteId) => {
    try {
      await deleteSite(siteId);
      alert("  .");
    } catch (error) {
      alert("    .");
    }
  };

  //   
  const regionOptions = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];

  const [showSupportDialog, setShowSupportDialog] = useState(false);
  const [supportformData, setSupportFormData] = useState({
    title: "",
    link: "",
    region: "",
  });

  //    
  const handleSaveSupportBoard = async () => {
    if (!supportformData.title || !supportformData.link) {
      alert("  .");
      return;
    }

    try {
      if (isEditMode) {
        const response = await updatesupportBoard(
          editingSite?.supportId,
          supportformData.title,
          supportformData.link,
          supportformData.region
        );
        console.log("   : ", response);
        alert(" .");
      } else {
        console.log("  ");

        const response = await addSupportBoard(
          supportformData.title,
          supportformData.link,
          supportformData.region
        );
        console.log("   : ", response);
        alert(" .");
      }
    } catch (error) {
      console.error("API  : ", error.response?.data || error.message);
      alert("   .");
    } finally {
      // Dialog 
      setShowSupportDialog(false);
      setSupportFormData({ title: "", link: "", region: "" });
      setIsEditMode(false);
      setEditingSite(null);
    }
  };

  const handleDeleteSupportBoard = async (supportId) => {
    try {
      await deleteSupportBoard(supportId);
      alert("  .");
    } catch (error) {
      alert("    .");
    }
  };

  return (
    <div className="pages-container">
      <div className="pages-header card">
        <span className="header-title">   - </span>
        <div className="header-selects">
          <Dropdown
            value={region}
            options={regionOptions}
            onChange={(e) => setRegion(e.value)}
            placeholder=" "
            className="dropdown"
          />
        </div>
      </div>
      <div className="card">
        <Panel header=" " toggleable className="panel">
          <div className="panel-header">
            <Button
              label=""
              icon="pi pi-plus"
              onClick={() => {
                setIsEditMode(false);
                setShowDialog(true);
              }}
            />
            <Button
              label=" "
              icon="pi pi-save"
              onClick={saveNewOrder}
              className="p-button-success"
            />
          </div>
          <DragDropContext onDragEnd={onDragEnd}>
            <div
              className="scroll-container"
              style={{ overflowY: "auto", height: "700px" }}
            >
              <Droppable droppableId="siteList">
                {(provided) => (
                  <div
                    {...provided.droppableProps}
                    ref={provided.innerRef}
                    className="editable-table"
                  >
                    {siteList.map((site, index) => (
                      <Draggable
                        key={site.siteInfoId}
                        draggableId={String(site.siteInfoId)}
                        index={index}
                      >
                        {(provided) => (
                          <div
                            className="draggable-item"
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <div className="site-item card">
                              <div className="content">
                                <img src={site.imgLink} alt={site.siteName} />
                                <div className="text">
                                  <span className="title">
                                     : {site.siteName}
                                  </span>
                                  <span className="title">
                                     : {site.siteLink}
                                  </span>
                                </div>
                              </div>
                              <div className="action">
                                <Button
                                  label=""
                                  icon="pi pi-pencil"
                                  className="p-button-text button"
                                  onClick={() => {
                                    setIsEditMode(true);
                                    setEditingSite(site);
                                    setFormData({
                                      img: "", //        
                                      link: site.siteLink,
                                      title: site.siteName,
                                      description: site.siteDescription,
                                    });
                                    setShowDialog(true);
                                  }}
                                />
                                <Button
                                  label=""
                                  icon="pi pi-trash"
                                  className="p-button-text p-button-danger button"
                                  onClick={() =>
                                    handleDeleteSite(site.siteInfoId)
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        )}
                      </Draggable>
                    ))}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </DragDropContext>
        </Panel>
      </div>
      <div className="card">
        <Panel header=" " toggleable className="panel">
          <div className="panel-header">
            <Button
              label=""
              icon="pi pi-plus"
              onClick={() => {
                setIsEditMode(false);
                setShowSupportDialog(true);
              }}
            />
          </div>
          <DataTable
            value={supportList}
            paginator
            rows={5}
            className="basic-table"
            onRowClick={(e) => window.open(e.data.link, "_blank")}
            rowClassName={() => "table-row"}
            emptyMessage=" ."
          >
            <Column field="title" header="" />
            <Column field="region" header="" />
            <Column field="createdAt" header="" />
            <Column
              header=""
              body={(rowData) => (
                <div
                  style={{
                    display: "flex",
                    gap: "10px",
                    justifyContent: "center",
                  }}
                >
                  <Button
                    label=""
                    icon="pi pi-pencil"
                    className="p-button-text"
                    onClick={() => {
                      setIsEditMode(true);
                      setShowSupportDialog(true);
                      setSupportFormData({
                        title: rowData.title,
                        link: rowData.link,
                        region: rowData.region,
                      });
                      setEditingSite(rowData);
                    }} //    
                  />
                  <Button
                    label=""
                    icon="pi pi-trash"
                    className="p-button-text p-button-danger"
                    onClick={() => handleDeleteSupportBoard(rowData.supportId)} //    
                  />
                </div>
              )}
            />
          </DataTable>
        </Panel>
      </div>
      <Dialog
        header={isEditMode ? " " : " "}
        visible={showSupportDialog}
        onHide={() => {
          setShowSupportDialog(false);
          setSupportFormData({ title: "", link: "", region: "" });
          setIsEditMode(false); //   
          setEditingSite(null); //    
        }}
        className="site-info-popup"
      >
        {/*   */}
        <div className="p-field">
          <span></span>
          <InputText
            id="title"
            name="title"
            value={supportformData.title}
            onChange={(e) => handleInputChange(e, "support")}
            placeholder=" "
          />
        </div>

        {/*   */}
        <div className="p-field">
          <span></span>
          <InputText
            id="link"
            name="link"
            value={supportformData.link}
            onChange={(e) => handleInputChange(e, "support")}
            placeholder=" "
          />
        </div>

        <div className="p-field">
          <span></span>
          <Dropdown
            value={supportformData.region}
            options={regionOptions}
            onChange={(e) =>
              setSupportFormData((prev) => ({
                ...prev,
                region: e.value,
              }))
            }
            placeholder=" "
            className="dropdown"
          />
        </div>
        {/*     */}
        <div className="dialog-footer">
          <Button
            label=""
            icon="pi pi-check"
            onClick={handleSaveSupportBoard}
          />
          <Button
            label=""
            icon="pi pi-times"
            className="p-button-secondary"
            onClick={() => {
              setShowSupportDialog(false);
              setSupportFormData({ title: "", link: "", region: "" });
              setEditingSite(null); //  
            }}
          />
        </div>
      </Dialog>
      <Dialog
        header={isEditMode ? "  " : "  "}
        visible={showDialog}
        onHide={() => {
          setShowDialog(false);
          setFormData({ img: "", link: "", title: "", description: "" });
          setEditingSite(null); // editingSite 
        }}
        className="site-info-popup"
      >
        {/*   */}
        {isEditMode && (
          <div className="p-field">
            <span> </span>
            <div>
              <img
                src={editingSite?.imgLink || ""}
                alt=" "
                style={{
                  maxWidth: "100%",
                  maxHeight: "150px",
                  marginTop: "10px",
                }}
              />
            </div>
          </div>
        )}

        {/*   */}
        <div className="p-field">
          <span>
            {isEditMode ? (
              <>
                  <br />
                ( )
              </>
            ) : (
              " "
            )}
          </span>
          <input type="file" accept="image/*" onChange={handleFileUpload} />
          {fileError && <small className="p-error">{fileError}</small>}
        </div>

        {/*    */}
        {formData.img && (
          <div className="p-field">
            <span>{isEditMode ? " " : ""}</span>
            <div>
              <img
                src={formData.img}
                alt=" "
                style={{
                  maxWidth: "100%",
                  maxHeight: "150px",
                  marginTop: "10px",
                }}
              />
            </div>
          </div>
        )}

        {/*   */}
        <div className="p-field">
          <span></span>
          <InputText
            id="title"
            name="title"
            value={formData.title}
            onChange={(e) => handleInputChange(e, "site")}
            placeholder=" "
          />
        </div>

        {/*   */}
        <div className="p-field">
          <span></span>
          <InputText
            id="link"
            name="link"
            value={formData.link}
            onChange={(e) => handleInputChange(e, "site")}
            // onChange={handleInputChange}
            placeholder=" "
          />
        </div>

        {/*   */}
        <div className="p-field">
          <span></span>
          <InputTextarea
            id="description"
            name="description"
            value={formData.description}
            onChange={(e) => handleInputChange(e, "site")}
            // onChange={handleInputChange}
            placeholder=" "
            rows={2}
          />
        </div>

        {/*     */}
        <div className="dialog-footer">
          <Button label="" icon="pi pi-check" onClick={handleSiteSave} />
          <Button
            label=""
            icon="pi pi-times"
            className="p-button-secondary"
            onClick={() => {
              setShowDialog(false);
              setFormData({ img: "", link: "", title: "", description: "" });
              setEditingSite(null); // editingSite 
            }}
          />
        </div>
      </Dialog>
    </div>
  );
}

export default HelpInfoAdmin;
