// src/pages/HelpInfo/HelpInfo.js
import { React, useState, useEffect } from "react";
import { useHelpInfo } from "../../hooks/useHelpInfo";
import { Dropdown } from "primereact/dropdown";
import { Panel } from "primereact/panel";
import { Carousel } from "primereact/carousel";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import "./HelpInfo.scss";

function HelpInfo() {
  const { siteList, supportList, region, setRegion } = useHelpInfo();
  const [numVisible, setNumVisible] = useState(3); //  

  useEffect(() => {
    const updateNumVisible = () => {
      const width = window.innerWidth;
      if (width >= 1600) {
        setNumVisible(6); //   
      } else if (width >= 1200) {
        setNumVisible(4); //  
      } else if (width >= 768) {
        setNumVisible(3); //  
      } else if (width >= 500) {
        setNumVisible(2); //  
      } else {
        setNumVisible(1); //  
      }
    };

    //     
    updateNumVisible(); //    
    window.addEventListener("resize", updateNumVisible);

    // 
    return () => {
      window.removeEventListener("resize", updateNumVisible);
    };
  }, []);
  //   
  const regionOptions = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];

  const siteTemplate = (siteList) => (
    <div
      className="helpInfo-carousel-card card"
      onClick={() => window.open(siteList.siteLink, "_blank")}
    >
      <img
        src={siteList.imgLink}
        alt={siteList.siteName}
        className="helpInfo-carousel-img"
      />
      <h4 className="helpInfo-carousel-title">{siteList.siteName}</h4>
      <p className="helpInfo-carousel-description">
        {siteList.siteDescription}
      </p>
    </div>
  );

  return (
    <div className="pages-container">
      <div className="pages-header card">
        <span className="header-title">  </span>
        <div className="header-selects">
          <Dropdown
            value={region}
            options={regionOptions}
            onChange={(e) => setRegion(e.value)}
            placeholder=" "
            className="dropdown"
          />
        </div>
      </div>
      <div className="card">
        <Panel header=" " toggleable className="panel">
          <Carousel
            key={numVisible}
            value={siteList}
            className="helpInfo-carousel"
            numVisible={numVisible}
            numScroll={1} //     
            circular
            autoplayInterval={5000}
            itemTemplate={siteTemplate}
          />
        </Panel>
      </div>
      <div className="card">
        <Panel header=" " toggleable className="panel">
          <DataTable
            value={supportList}
            paginator
            rows={5}
            className="basic-table"
            onRowClick={(e) => window.open(e.data.link, "_blank")}
            rowClassName={() => "table-row"}
            emptyMessage=" ."
          >
            <Column field="title" header="" />
            <Column field="region" header="" />
            <Column field="createdAt" header="" />
          </DataTable>
        </Panel>
      </div>
    </div>
  );
}

export default HelpInfo;
