// src/hooks/useHelpInfo.js"
import { useState, useEffect, useRef } from "react";
import {
  getInfoSiteList,
  siteAdd,
  siteDelete,
  siteNewOrderSave,
  siteUpdate,
  getSupportBoardList,
  supportBoardAdd,
  supportBoardUpdate,
  supporBoardDelete,
} from "../service/helpInfoApi";
import { useGlobalState } from "../context/GlobalStateContext";

export function useHelpInfo() {
  const { updateGlobalState } = useGlobalState(); //    
  const [siteList, setSiteList] = useState([]);
  const [region, setRegion] = useState("");
  const [supportList, setSupportList] = useState([]);

  const isInitialRender = useRef(true); //    

  useEffect(() => {
    fetchSiteList(); //   
    fetchSupportList(region); //    
  }, []); //    

  useEffect(() => {
    console.log(region);
    if (isInitialRender.current) {
      isInitialRender.current = false; //    false 
    } else {
      fetchSupportList(region); //    region   
    }
  }, [region]); // region   

  const fetchSiteList = async () => {
    updateGlobalState("isLoading", true);
    try {
      const data = await getInfoSiteList();
      setSiteList(data);
    } catch (error) {
      console.error("  :", error);
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const addSite = async (img, link, title, description) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await siteAdd(img, link, title, description);
      if (data.code === 200) {
        fetchSiteList();
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const deleteSite = async (siteId) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await siteDelete(siteId);
      if (data.code === 200) {
        fetchSiteList();
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const saveNewOrder = async () => {
    updateGlobalState("isLoading", true);
    try {
      const orderPayload = siteList.map((site) => ({
        siteInfoId: site.siteInfoId,
        indexOrder: site.indexOrder,
      }));

      const data = await siteNewOrderSave(orderPayload);
      if (data.code === 200) {
        console.log("  :", siteList);
      } else {
        console.error("  :", data.message);
      }
      return data;
    } catch (error) {
      console.error("  API  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const updateSite = async (siteInfoId, img, link, title, description) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await siteUpdate(siteInfoId, img, link, title, description);
      if (data.code === 200) {
        fetchSiteList();
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const fetchSupportList = async (region) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await getSupportBoardList(region);
      setSupportList(data);
    } catch (error) {
      console.error("  :", error);
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const addSupportBoard = async (title, link, region) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await supportBoardAdd(title, link, region);
      if (data.code === 200) {
        // fetchSupportList(region);
        setRegion(region);
      }
      return data;
    } catch (error) {
      console.error("   :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const deleteSupportBoard = async (supportId) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await supporBoardDelete(supportId);
      if (data.code === 200) {
        fetchSupportList(region);
      }
      return data;
    } catch (error) {
      console.error("   :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const updatesupportBoard = async (supportId, title, link, region) => {
    updateGlobalState("isLoading", true);

    try {
      const data = await supportBoardUpdate(supportId, title, link, region);
      console.log(data);
      if (data.code === 200) {
        fetchSupportList(region);
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  return {
    region,
    setRegion,
    siteList,
    setSiteList,
    addSite,
    deleteSite,
    saveNewOrder,
    updateSite,
    supportList,
    setSupportList,
    fetchSupportList,
    addSupportBoard,
    deleteSupportBoard,
    updatesupportBoard,
  };
}
